import { Component, Input } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, Nav, ToastController, MenuController, LoadingController } from 'ionic-angular';

import { User } from '../../providers';
import { MainPage } from '../';


@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {
  // The account fields for the login form.
  // If you're using the username field with or without email, make
  // sure to add it to the type

  @Input() data: any;
  @Input() events: any;


  private isUsernameValid: boolean = true;
  private isPasswordValid: boolean = true;

  account: { username: string, password: string } = {
    username: null,
    password: null
  };

  // Our translated text strings
  private loginErrorString: string;

  constructor(public navCtrl: Nav,
    public user: User,
    public toastCtrl: ToastController,
    public translateService: TranslateService,
    public menu: MenuController,
    public loadingCtrl: LoadingController) {

    this.translateService.get('LOGIN_ERROR').subscribe((value) => {
      this.loginErrorString = value;
    })
  }

  // Attempt to login in through our User service
  doLogin() {
    if (!this.validate()) {
      return;
    }
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    this.user.login(this.account).subscribe((resp) => {
      this.navCtrl.setRoot(MainPage);
    }, (err) => {
      //this.navCtrl.push(MainPage);
      // Unable to log in
      loading.dismiss();
      let toast = this.toastCtrl.create({
        message: this.loginErrorString,
        duration: 6000,
        position: 'top'
      });
      toast.present();
    }, () => { loading.dismiss(); });
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page
    this.menu.enable(false);
  }


  validate():boolean {
    this.isUsernameValid = true;
    this.isPasswordValid = true;

    if (!this.account.username ||this.account.username.length == 0) {
        this.isUsernameValid = false;
    }

    if (!this.account.password || this.account.password.length == 0) {
        this.isPasswordValid = false;
    }

    return this.isPasswordValid && this.isUsernameValid;
 }
}



